export default "\r\n/* Space out content a bit */\r\nbody {\r\n  padding-top: 20px;\r\n  padding-bottom: 20px;\r\n}\r\n/* Everything but the jumbotron gets side spacing for mobile first views */\r\n.header,\r\n.marketing,\r\n.footer {\r\n  padding-right: 15px;\r\n  padding-left: 15px;\r\n}\r\n/* Custom page header */\r\n.header {\r\n  border-bottom: 1px solid #e5e5e5;\r\n}\r\n/* Make the masthead heading the same height as the navigation */\r\n.header h3 {\r\n  padding-bottom: 19px;\r\n  margin-top: 0;\r\n  margin-bottom: 0;\r\n  line-height: 40px;\r\n}\r\n/* Custom page footer */\r\n.footer {\r\n  padding-top: 19px;\r\n  color: #777;\r\n  border-top: 1px solid #e5e5e5;\r\n}\r\n/* Customize container */\r\n@media (min-width: 768px) {\r\n  .container {\r\n    max-width: 730px;\r\n  }\r\n}\r\n.container-narrow > hr {\r\n  margin: 30px 0;\r\n}\r\n/* Main marketing message and sign up button */\r\n.jumbotron {\r\n  text-align: center;\r\n  border-bottom: 1px solid #e5e5e5;\r\n}\r\n.jumbotron .btn {\r\n  padding: 14px 24px;\r\n  font-size: 21px;\r\n}\r\n/* Supporting marketing content */\r\n.marketing {\r\n  margin: 40px 0;\r\n}\r\n.marketing p + h4 {\r\n  margin-top: 28px;\r\n}\r\n/* Responsive: Portrait tablets and up */\r\n@media screen and (min-width: 768px) {\r\n  /* Remove the padding we set earlier */\r\n  .header,\r\n  .marketing,\r\n  .footer {\r\n    padding-right: 0;\r\n    padding-left: 0;\r\n  }\r\n  /* Space out the masthead */\r\n  .header {\r\n    margin-bottom: 30px;\r\n  }\r\n  /* Remove the bottom border on the jumbotron for visual effect */\r\n  .jumbotron {\r\n    border-bottom: 0;\r\n  }\r\n}\r\n/*# sourceMappingURL=data:application/json;base64, */"